<template>
  <div>
    {{ data }}
  </div>
</template>

<script>
import axios from 'axios';
import cheerio from 'cheerio';
export default {
  name: 'TestAPI',
  data() {
    return {
      cors: 'https://cors-anywhere.herokuapp.com/',
      baseUrl: 'https://xosodaiphat.com/xsmt-truc-tiep.html',
      channels: ['A', 'B', 'C'],
      pre_data: null,
      data: null,
      interval: null
    };
  },
  created() {
    this.loadData();
  },
  async mounted() {
    this.interval = setInterval(await this.loadData, 10000);
  },
  methods: {
    loadData: function() {
      var pre_data = this.data;
      var data = [];
      for (var i = 0; i < this.channels.length; ++i) {
        var tmp = [[], [], [], [], [], [], [], [], []];
        data.push(tmp.concat());
      }
      axios(this.cors + this.baseUrl)
        .then(response => {
          const $ = cheerio.load(response.data);
          $('.table-striped tbody > tr').each((pos, val) => {
            const $1 = cheerio.load(val);
            $1('td').next((pos1, val1) => {
              const $2 = cheerio.load(val1);
              $2('span').each((pos2, val2) => {
                data[pos1][pos].push($(val2).text());
              });
            });
          });
          if (pre_data !== null) {
            for (var i = 0; i < this.channels.length; ++i) {
              for (var j = 0; j < 9; ++j) {
                for (var k = 0; k < data[i][j].length; ++k)
                if (data[i][j][k] !== '...') {
                  pre_data[i][j][k] = data[i][j][k];
                  console.log(pre_data[i][j][k]);
                }
              }
            }
          } else {
            pre_data = data;
          }
          this.data = pre_data;
          // this.handleData(data);
          console.log(new Date());
          // console.log(data);
          // return data;
        })
        .catch(console.error);
    },
    handleData(data) {
      if (this.pre_data !== undefined) {
        for (var i = 0; i < this.channels.length; ++i) {
          for (var j = 0; j < 9; ++j) {
            if (data[i][j] !== '...') {
              this.pre_data[i][j] = data[i][j];
            }
          }
        }
      }
    }
  },
  beforeDestroy: function() {
    clearInterval(this.interval);
  }
};
</script>
