<template>
  <div>
    <navbar />
    <div class='content'>
      <div v-if='gotData'>
        <div>
          <div v-if='validData'>
            <table class='table table-bordered text-center'>
              <thead>
                <tr>
                  <th scope='col' width='4%' class='channel'>
                    <router-link to='/middle/0/0'>
                      <home></home>
                    </router-link>
                  </th>
                  <th
                    v-for='(channel, index) in channels'
                    :key='index'
                    scope='col'
                    :width='colWidth'
                    class='channel'
                  >
                    {{ channel }}
                  </th>
                </tr>
              </thead>
              <tbody>
                <tr
                  v-for='(rewardName, rewardIndex) in rewardNames'
                  :key='rewardIndex'
                >
                  <td>{{ rewardName }}</td>
                  <td
                    v-for='(channel, channelIndex) in channels'
                    :key='channelIndex'
                    :style='{ color: getTextColor(rewardIndex) }'
                  >
                    <div
                      v-for='(res, index2) in data[channelIndex][rewardIndex]'
                      :key='index2'
                    >
                      {{ res }}
                    </div>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
          <div v-else class='notValid'>
            <router-link to='/middle/0/0'>
              <home></home>
            </router-link>
            <error></error>
          </div>
        </div>
      </div>
      <div v-else>
        <loading></loading>
      </div>
    </div>
  </div>
</template>

<script>
import home from './Home.vue';
import loading from './Loading.vue';
import error from './Error.vue';
import navbar from './NavBar.vue';
import axios from 'axios';
export default {
  name: 'middle',
  components: {
    navbar,
    home,
    loading,
    error
  },
  data: function() {
    return {
      apiURI: '/api/lotteries/middle/#date',
      channels: [],
      data: [],
      rewardNames: null,
      colWidth: '32%',
      gotData: false,
      validData: false
    };
  },
  watch: {
    channels: function(val) {
      const len = val.length;
      if (len === 3) {
        this.colWidth = '32%';
      } else if (len === 4) {
        this.colWidth = '24%';
      }
    }
  },
  created() {
    const date = this.$route.params.dateString;
    const url = this.apiURI.replace(/#date/g, date);
    this.getData(url);
  },
  methods: {
    getData(url) {
      this.gotData = false;
      axios(url)
        .then(response => {
          let res = response.data;
          let channels = [];
          let data = [];
          let rewardNames = [];
          res.forEach(channel => {
            let tmp = [];
            for (const pro in channel) {
              if (pro !== '_id' && pro !== '__v' && pro !== 'date') {
                if (pro === 'channel') {
                  channels.push(channel[pro]);
                } else {
                  if (!rewardNames.includes(pro)) {
                    rewardNames.push(pro);
                  }
                  tmp.push(channel[pro]);
                }
              }
            }
            data[channels.length-1] = [...tmp];
          });
          this.data = data;
          this.channels = channels;
          this.rewardNames = rewardNames;
          this.validData = data[0][0].length > 0;
          this.gotData = true;
        })
        .catch(console.error);
    },
    getTextColor(rewardIndex) {
      if (rewardIndex < 1 || rewardIndex > this.rewardNames.length - 2) {
        return 'red';
      }
      return 'black';
    }
  }
};
</script>
