<template>
  <nav class='panel-footer clearfix fixed-bottom'>
    <b-button-group class='custom-nav'>
      <b-dropdown
        :text='selectedRegion'
        class='region'
        variant='primary'
        :size='size'
        id='dropdown-dropup'
        dropup
      >
        <b-dropdown-item
          v-for='(region, index) in regions'
          :key='index'
          @click='selectRegion(region)'
          :disabled='region === selectedRegion ? true : false'
        >
          {{ region }}
        </b-dropdown-item>
      </b-dropdown>

      <b-dropdown
        :text='selectedDoW'
        class='DoW'
        variant='secondary'
        :size='size'
        id='dropdown-dropup'
        dropup
      >
        <b-dropdown-item
          v-for='(DoW, index) in DoWs'
          :key='index'
          @click='selectDoW(DoW)'
          :disabled='DoW === selectedDoW ? true : false'
        >
          {{ DoW }}
        </b-dropdown-item>
      </b-dropdown>

      <b-dropdown
        :text='selectedDateString'
        class='date'
        variant='success'
        :size='size'
        id='dropdown-dropup'
        dropup
      >
        <b-dropdown-item
          v-for='(dateString, index) in dateStrings'
          @click='selectDate(dateString)'
          :key='index'
          :disabled='dateString === selectedDateString ? true : false'
        >
          {{ dateString }}
        </b-dropdown-item>
      </b-dropdown>
    </b-button-group>
  </nav>
</template>

<script>
export default {
  name: 'NavBar',
  data: function() {
    return {
      regions: ['Miền Bắc', 'Miền Trung', 'Miền Nam'],
      regionCodes: ['north', 'middle', 'south'],
      selectedRegion: '',
      DoWs: ['Thứ 2', 'Thứ 3', 'Thứ 4', 'Thứ 5', 'Thứ 6', 'Thứ 7', 'Chủ nhật'],
      DoWcodes: [
        'thu-2',
        'thu-3',
        'thu-4',
        'thu-5',
        'thu-6',
        'thu-7',
        'chu-nhat'
      ],
      selectedDoW: '',
      dateStrings: [],
      selectedDateString: '',
      timeRes: 17,
      size: 'lg'
    };
  },
  mounted() {
    window.addEventListener('resize', () => {
      this.changeNavSize();
    });
  },
  watch: {
    selectedRegion(val) {
      if (val === 'Miền Nam') {
        this.timeRes = 17;
      } else if (val === 'Miền Trung') {
        this.timeRes = 18;
      } else {
        this.timeRes = 19;
      }
    }
  },
  created() {
    this.changeNavSize();
    const name = this.$route.name;
    if (name === 'home') {
      this.selectedRegion = this.regions[2];
      this.selectedDoW = this.getCurDoW();
      let today = new Date();
      if (today.getHours() < this.timeRes) {
        today = new Date(today.getTime() - 7 * 24 * 60 * 60 * 1000);
      }
      this.moveToNewPage(this.getStringFromDate(today));
    } else {
      this.selectedRegion = this.regions[this.regionCodes.indexOf(name)];
      const DoWcode = this.$route.params.DoWcode;
      if (DoWcode === '0') {
        this.selectedDoW = this.getCurDoW();
        let today = new Date();
        if (today.getHours() < this.timeRes) {
          today = new Date(today.getTime() - 7 * 24 * 60 * 60 * 1000);
        }
        this.moveToNewPage(this.getStringFromDate(today));
      }
      const DoWindex = this.DoWcodes.indexOf(DoWcode);
      this.selectedDoW = this.DoWs[DoWindex];
      this.selectedDateString = this.$route.params.dateString;
      this.genDateStringsFromDoW(this.selectedDoW);
    }
  },
  methods: {
    changeNavSize() {
      const width = window.innerWidth > 0 ? window.innerWidth : screen.width;
      if (width < 500) {
        this.size = 'sm';
      } else {
        this.size = 'lg';
      }
    },
    getDoWindexFromDate(val) {
      return (val.getDay() + 7 - 1) % 7;
    },
    getCurDoW() {
      return this.DoWs[this.getDoWindexFromDate(new Date())];
    },
    formatDigits(val, digits) {
      let res = val.toString();
      while (res.length < digits) {
        res = '0' + res;
      }
      return res;
    },
    getStringFromDate(val) {
      const day = this.formatDigits(val.getDate(), 2);
      const month = this.formatDigits(val.getMonth() + 1, 2);
      const year = val.getFullYear();
      return day + '-' + month + '-' + year;
    },
    genDateStringsFromDoW(DoW) {
      const today = new Date();
      const offset = this.DoWs.indexOf(DoW) - this.getDoWindexFromDate(today);
      const day = 24 * 60 * 60 * 1000;
      let date = new Date(today.getTime() + offset * day);
      if (offset > 0 || (offset === 0 && today.getHours() < this.timeRes)) {
        date = new Date(date - 7 * day);
      }
      this.dateStrings = [];
      for (let i = 0; i < 7; ++i) {
        this.dateStrings.push(this.getStringFromDate(date));
        date = new Date(date - 7 * day);
      }
    },
    moveToNewPage(dateString) {
      const regionIndex = this.regions.indexOf(this.selectedRegion);
      const regionCode = this.regionCodes[regionIndex];
      const DoWcode = this.DoWcodes[this.DoWs.indexOf(this.selectedDoW)];
      this.$router.push({
        name: regionCode,
        params: { DoWcode: DoWcode, dateString: dateString }
      });
    },
    selectRegion(val) {
      this.selectedRegion = val;
      this.moveToNewPage(this.selectedDateString);
    },
    selectDoW(val) {
      this.selectedDoW = val;
      this.genDateStringsFromDoW(val);
      this.moveToNewPage(this.dateStrings[0]);
    },
    selectDate(val) {
      this.selectedDateString = val;
      this.moveToNewPage(val);
    }
  }
};
</script>
